import React, { useState } from 'react'
import { ButtonGroup, ToggleButton } from 'react-bootstrap'

export const SingleToggleButton = ({ input, label, value, className, name, icon, positiveIcon, positiveClass }) => {

    const handleAlignment = (e) => {
        console.log(e)
        input.onChange(e);
      };
      const classes = icon ? " icon-btn" : " btn themeButton ";
      const classNames = icon ? "default-icon-button" : className + " " + positiveClass;
    return (
        <ButtonGroup toggle className="mb-2">
        <ToggleButton
          name={name}
          type="checkbox"
          className={classNames + classes}
          checked={input?.value ? input?.value : false}
          onChange={(e) => handleAlignment(e.currentTarget.checked)}
        >
          {!positiveIcon ? label : (input?.value ? positiveIcon : label ) }
          {/* {label} */}
        </ToggleButton>
      </ButtonGroup>
    )
}

SingleToggleButton.defaultProps = {
  classNames: "lightGrey-button",
 };