import React, { Fragment, useState, useEffect } from "react";
import {
  ButtonGroup,
  ToggleButton,
  Form,
} from "react-bootstrap";
import ThemeButton from "../../../partials/button/ThemeButton";
import cuid from "cuid";

import * as diaryRedux from "../_redux/diaryRedux";
import { useDispatch } from "react-redux";
const CustomToolbar = (props) => {

  const [diaryType, setDiaryType] = useState(null);
  const [diaryOf, setDiaryOf] = useState(null);
  const [diaryView, setDiaryView] = useState(null);

  useEffect(() => {
      console.log(props)
    setDiaryType(props.diaryType)
    setDiaryOf(props.diaryOf)
    setDiaryView(props.view)
  }, []);

  console.log(props);
  
  const navigates = {
    PREVIOUS: "PREV",
    NEXT: "NEXT",
    TODAY: "TODAY",
    DATE: "DATE",
  };
  const diaryTypeOptions = [
    { name: "Appts", value: "appts" },
    { name: "Tasks", value: "tasks" },
  ];

  const viewsOptions = [
    { name: "Day", value: "day" },
    { name: "Week", value: "week" },
    { name: "Month", value: "month" },
  ];

  const dispatch = useDispatch();


  const {
    localizer: { messages },
    label,
  } = props;

  const navigate = (action) => {
    console.log(action);
    props.onNavigate(action);
  };
  const view = (action) => {
    props.onView(action);
  };

  const handleDiaryType = (e) => {
    setDiaryType(e.currentTarget.value);
    props.handleDiaryType(e.currentTarget.value)
    dispatch(
      diaryRedux.actions.handleDiaryType(e.currentTarget.value)
    );
  };

  const handleDiaryOf = (e) => {
    setDiaryOf(e.currentTarget.value);
    props.handleDiaryOf(e.currentTarget.value)
  };

  const handleDiaryView = (e) => {
    setDiaryView(e.currentTarget.value);
    props.onView(e.currentTarget.value);
  };

  const viewNamesGroup = (messages) => {
    let viewNames = props.views;
    const view = props.view;

    if (viewNames.length > 1) {
      return viewNames.map((name) => (
        <ToggleButton key={name} onClick={this.view.bind(null, name)}>
          {messages[name]}
        </ToggleButton>
      ));
    }
  };

  return (
    <Fragment>
      <div className="rbc-toolbar">
        {/* <span className="rbc-btn-group">
          <button
            type="button"
            onClick={this.navigate.bind(null, navigate.PREVIOUS)}
          >
            Prev
          </button>
        </span> */}
        <div className="filter-container">
        <ButtonGroup className="themeButton-toggle-group pr-2" toggle>
          {viewsOptions.map((view, idx) => (<Fragment>
            <ToggleButton
              key={cuid()}
              type="radio"
              name="viewsOption"
              value={view.value}
              checked={diaryView === view.value}
              onChange={handleDiaryView}
            >
              {view.name}
            </ToggleButton>
          </Fragment>))}
           </ButtonGroup> 
          <ThemeButton
            className="darkGrey-button"
            onClick={props.handleChange}
          >
            Filter
          </ThemeButton>
        </div>
        <span className="rbc-toolbar-label">{label}</span>
        {/* <span className="rbc-btn-group">
          <button
            type="button"
            onClick={this.navigate.bind(null, navigate.NEXT)}
          >
            Next
          </button>
        </span> */}
        <ButtonGroup className="navigation pl-2">
          <ThemeButton icon onClick={() => navigate(navigates.PREVIOUS)}>
          <i class="fas fa-angle-left"></i>
          </ThemeButton>
          <ThemeButton>Today</ThemeButton>
          <ThemeButton icon onClick={() => navigate(navigates.NEXT)}>
          <i class="fas fa-angle-right"></i>
          </ThemeButton>
        </ButtonGroup>

        <ButtonGroup className="themeButton-toggle-group pl-2" toggle>
          {diaryTypeOptions.map((radio, idx) => (
            <ToggleButton
              key={cuid()}
              type="radio"
              name="radio"
              value={radio.value}
              checked={diaryType === radio.value}
              onChange={handleDiaryType}
            >
              {radio.name}
            </ToggleButton>
          ))}
        </ButtonGroup>
        <div class="pl-2">
          <Form.Control
            className="selectOption"
            as="select"
            defaultValue={diaryOf}
            onChange={handleDiaryOf}
          >
            <option value="self">Self</option>
            <option value="team">Team</option>
          </Form.Control>
        </div>
      </div>
    </Fragment>
  );
};

export default CustomToolbar;
