import React, { Fragment, useState } from "react";
import { Col, Row } from "react-bootstrap";
import Item from "../../../../../../partials/display/Item";
import { Field } from "redux-form";
import TextArea from "../../../../../../partials/form/TextArea";
import MultiSelect from "../../../../../../partials/form/MultiSelect";
import ThemeHeader from "../../../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../../../partials/button/ThemeButton";
import DateInput from "../../../../../../partials/form/DateInput";

const NewToDoTask = (props) => {
  const {pristine, submitting, invalid, onHide } = props;
  const repeatOptions = [
    {
      label: "Never",
      value: "never",
    },
    {
      label: "Weekly",
      value: "weekly",
    },
    {
      label: "Monthly",
      value: "monthly",
    },
  ];

  const toOptions = [
    {
      label: "Alb",
      value: "alb",
    },
    {
      label: "Open Buyers",
      value: "2",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];

  React.useEffect(() => {
    if (!props.data?.id) {
      setEdit(true);
    }
  }, []);
  const [edit, setEdit] = useState(false);
  return (
    <Fragment>
      <ThemeHeader
        left={
          <h5 className="title">{!props.data?.id ? "New" : ""} To Do Task</h5>
        }
        right={
          <Fragment>
            {!props.data?.id ? (
              <Fragment>
                {" "}
                <div className="pl-2">
                  <ThemeButton onClick={onHide} className="lightGrey-button">
                    Cancel
                  </ThemeButton>
                </div>{" "}
                <div className="pl-2">
                  <ThemeButton
                    disabled={invalid || pristine || submitting}
                    type="submit"
                    className="green-button"
                  >
                    Save
                  </ThemeButton>
                </div>
              </Fragment>
            ) : (
              <Fragment>
                {!edit ? (
                  <div className="pl-2">
                    <ThemeButton
                      onClick={() => setEdit(!edit)}
                      icon
                      className="lightGrey-button"
                    >
                      <i class="fas fa-minus-circle"></i>
                    </ThemeButton>
                  </div>
                ) : (
                  ""
                )}

                <div className="pl-2">
                  <ThemeButton onClick={onHide} className="lightGrey-button">
                    Cancel
                  </ThemeButton>
                </div>
                <div className="pl-2">
                  <ThemeButton onClick={onHide} className="darkGrey-button">
                    Delete
                  </ThemeButton>
                </div>
                <div className="pl-2">
                  <ThemeButton
                    disabled={invalid || pristine || submitting}
                    type="submit"
                    className="green-button"
                  >
                    Save
                  </ThemeButton>
                </div>
              </Fragment>
            )}
          </Fragment>
        }
      />
      {edit ? (
        <Fragment>
          {" "}
          <Row>
            <Col>
              <Item
                left="Task"
                lg={2}
                md={3}
                right={
                  <Field
                    name="task"
                    component={TextArea}
                    placeholder="Due Date"
                    row={4}
                  />
                }
              />
            </Col>
          </Row>
          <Row>
            {props.state.diaryOf === "team" ? (
              <Col>
                <Item
                  lg={4}
                  md={6}
                  left="To"
                  right={
                    <Field
                      name="to"
                      isMulti={false}
                      component={MultiSelect}
                      showBottom={false}
                      options={toOptions}
                      defaultValue="Never"
                    />
                  }
                />
              </Col>
            ) : (
              ""
            )}
            <Col>
              <Item
                lg={4}
                md={6}
                left="Due Date"
                right={
                  <Field
                    name="dueDate"
                    type="text"
                    component={DateInput}
                    placeholder="Due Date"
                  />
                }
              />
            </Col>
            <Col>
              <Item
                left="Repeat"
                right={
                  <Field
                    name="repeat"
                    isMulti={false}
                    component={MultiSelect}
                    showBottom={false}
                    options={toOptions}
                    defaultValue="Alb"
                  />
                }
              />
            </Col>
          </Row>
        </Fragment>
      ) : (
        <Fragment>
          <Row>
            <Col>
              <Item
                left="Task"
                lg={2}
                md={3}
                right={
                  <Field
                    name="task"
                    component={TextArea}
                    edit={false}
                    placeholder="Due Date"
                    row={4}
                  />
                }
              />
            </Col>
          </Row>

          <Row>
            {props.state.diaryOf === "team" ? (
              <Col>
                <Item
                  lg={4}
                  md={6}
                  left="To"
                  right={
                    <Field
                      name="to"
                      isMulti={false}
                      component={MultiSelect}
                      showBottom={false}
                      options={toOptions}
                      defaultValue="Never"
                    />
                  }
                />
              </Col>
            ) : (
              ""
            )}
            <Col>
              <Item
                lg={4}
                md={6}
                left="Due Date"
                right={
                  <Field
                    name="dueDate"
                    type="text"
                    component={DateInput}
                    placeholder="Due Date"
                    edit={false}
                  />
                }
              />
            </Col>
            <Col>
              <Item
                left="Repeat"
                right={
                  <Field
                    name="repeat"
                    isMulti={false}
                    component={MultiSelect}
                    showBottom={false}
                    options={toOptions}
                    defaultValue="Alb"
                  />
                }
              />
            </Col>
          </Row>
        </Fragment>
      )}
    </Fragment>
  );
};

export default NewToDoTask;
