import React, { Fragment } from "react";
import { convertToRaw } from 'draft-js';
import { Editor } from 'react-draft-wysiwyg';
import draftToMarkdown from 'draftjs-to-markdown';
import ThemeButton from "../button/ThemeButton";
import { Form, Row } from "react-bootstrap";

const TextAreaEmoji = ({
  input,
  rows,
  classnames,
  placeholder,
  meta: { touched, error },
  edit,
}) => {

  console.log(input);
  return (
    <Fragment>
       
      {edit ? (
        <Fragment>
           <div className={"d-flex textAreaEmoji " + classnames}>
         <Editor
          wrapperClassName="textAreaEmoji-wrapper"
          editorClassName="textAreaEmoji-editor"
          onEditorStateChange={(e) => input.onChange(draftToMarkdown(convertToRaw(e.getCurrentContent())))}
          toolbar={{options: ['emoji']}}
        />
        </div>
          <Form.Control.Feedback type="invalid">
            {touched && error && <span>{error}</span>}
          </Form.Control.Feedback>
        </Fragment>
      ) : (
        <Fragment>
          <Row>
            <p>{input?.value}</p>
          </Row>
        </Fragment>
      )}
    </Fragment>
  );
};
TextAreaEmoji.defaultProps = {
  rows: "3",
  edit: true,
};

export default TextAreaEmoji;
