import React, { useState, Fragment, useCallback, Suspense } from "react";
import { Col, Row } from "react-bootstrap";
import { reduxForm, Field, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import ThemeHeader from "../../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../../partials/button/ThemeButton";
import Item from "../../../../../partials/display/Item";
import DateInput from "../../../../../partials/form/DateInput";
import TextInput from "../../../../../partials/form/TextInput";
import MultiSelect from "../../../../../partials/form/MultiSelect";
import TextArea from "../../../../../partials/form/TextArea";
import TypeList from "./type/TypeList";
import AppointmentAlerts from "./partials/AppointmentAlerts";
import DefaultModal from "../../../../../partials/modal/DefaultModal";
import PreviewEmailSms from "../previewEmailSms/PreviewEmailSms";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const Appointment = (props) => {
  const { handleSubmit, pristine, submitting, invalid, onHide } = props;

  // Preview Email Sms Modal
  const [modalShow, setModalShow] = useState(false);
  const handleModal = useCallback((e) => {
    // setModalData(e);
    setModalShow(true);
  }, []);
  //

  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);
  const myBuyerOptions = [
    {
      label: "General",
      value: "general",
    },
    {
      label: "OneToOne",
      value: "oneToOne",
    },
    {
      label: "Buyer",
      value: "buyer",
    },
    {
      label: "Listing",
      value: "listing",
    },

    {
      label: "MarketReview",
      value: "marketReview",
    },
    {
      label: "OpenHome",
      value: "openHome",
    },
    {
      label: "RentalOpenHome",
      value: "rentalOpenHome",
    },
    {
      label: "SellerVisit",
      value: "sellerVisit",
    },
    {
      label: "Tenant",
      value: "tenant",
    },
    {
      label: "VirtualListing",
      value: "virtualListing",
    },
    {
      label: "VirtualBuyer",
      value: "virtualBuyer",
    },
  ];
  const renderSwitch = (param) => {
    switch (param) {
      case "listing":
        return <AppointmentAlerts title="Prospects" />;
      case "buyer":
        return (
          <Fragment>
            <AppointmentAlerts title="Buyers" />
            <AppointmentAlerts title="Sellers" />
          </Fragment>
        );
      case "marketReview":
        return <AppointmentAlerts title="Sellers" />;
      case "oneToOne":
        return <AppointmentAlerts title="Team Member" />;
      case "rentalOpenHome":
        return <AppointmentAlerts title="Landlords" />;
      case "tenant":
        return (
          <Fragment>
            <AppointmentAlerts title="Tenants" />
            <AppointmentAlerts title="Landlords" />
          </Fragment>
        );
      case "sellerVisit":
        return <AppointmentAlerts title="Sellers" />;
      case "rental":
        return <AppointmentAlerts title="Landlords" />;
      case "virtualListing":
        return <AppointmentAlerts title="Prospects" />;
      case "virtualBuyer":
        return (
          <Fragment>
            <AppointmentAlerts title="Buyers" />
            <AppointmentAlerts title="Sellers" />
          </Fragment>
        );
      case "openHome":
        return <AppointmentAlerts title="Sellers" />;
      default:
        return "";
    }
  };
  return (
    <Fragment>
      {modalShow ? (
        <DefaultModal
          size="xl"
          centered
          show={modalShow}
          onHide={() => setModalShow(false)}
          classNames="borderLess previewSmsEmail"
        >
          <PreviewEmailSms  onHide={() => setModalShow(false)} />
        </DefaultModal>
      ) : (
        ""
      )}

      <form className="contactForm" onSubmit={handleSubmit(onFormSubmit)}>
        <ThemeHeader
          left={<h5 className="title">New Appointment</h5>}
          right={
            <Fragment>
              {" "}
              <div className="pl-2">
                <ThemeButton onClick={onHide} className="lightGrey-button">
                  Cancel
                </ThemeButton>
              </div>
              <div className="pl-2">
                <ThemeButton
                  disabled={invalid || pristine || submitting}
                  type="submit"
                  className="green-button"
                >
                  Save
                </ThemeButton>
              </div>
            </Fragment>
          }
        />

        <Row>
          <Col md={5}>
            <Item
              left="Type"
              right={
                <Field
                  name="type"
                  isMulti={false}
                  component={MultiSelect}
                  showBottom={false}
                  options={myBuyerOptions}
                />
              }
            />
            <Item
              left="For"
              right={
                <Field
                  name="for"
                  component={MultiSelect}
                  showBottom={false}
                  options={myBuyerOptions}
                  defaultValue="1"
                />
              }
            />
            <Item
              left="Title"
              right={<Field name="title" component={TextInput} />}
            />
            <Item
              left="Starts"
              right={<Field name="starts" component={DateInput} />}
            />
            <Item
              left="Ends"
              right={<Field name="ends" component={DateInput} />}
            />
            <Item
              left="Repeat"
              right={
                <Field
                  name="repeat"
                  component={MultiSelect}
                  showBottom={false}
                  options={myBuyerOptions}
                  defaultValue="1"
                />
              }
            />
            <Item
              left="Notes"
              right={<Field name="notes" component={TextArea} row={3} />}
            />
            <Item
              left="Attendance"
              right={
                <Field
                  name="attendance"
                  component={MultiSelect}
                  showBottom={false}
                  options={myBuyerOptions}
                  defaultValue="1"
                />
              }
            />

            <Item
              left="Alerts"
              right={
                <Fragment>
                  <Row>
                    <Col>Notify</Col>
                    <Col>Remind</Col>
                    <Col>
                      <ThemeButton
                        onClick={handleModal}
                        icon
                        className="darkGrey-icon-button"
                      >
                        <i class="fas fa-eye"></i>
                      </ThemeButton>
                    </Col>
                  </Row>
                </Fragment>
              }
            />
            {props.type?.value ? renderSwitch(props.type?.value) : ""}
          </Col>
          <Col md={7}>{props.type?.value ? <TypeList {...props} /> : ""}</Col>
        </Row>
      </form>
    </Fragment>
  );
};

const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};

const selector = formValueSelector("newAppointment");

const mapState = (state) => {
  const location = selector(state, "location");
  const type = selector(state, "type");
  return {
    location,
    type,
    initialValues: {
      type: {
        label: "General",
        value: "general",
      },
      location: "same",
    },
  };
};
export default connect(
  mapState,
  actions
)(reduxForm({ form: "newAppointment", validate })(Appointment));
