import React, { useCallback, useState, useEffect } from "react";
import { Field, reduxForm } from "redux-form";
import {
  ButtonGroup,
  ToggleButton,
} from "react-bootstrap";
import { SingleToggleButton } from "../../../partials/form/singleToggleButton/SingleToggleButton";
import { useSelector, shallowEqual } from "react-redux";


const DrawerForm = (props) => {
  const { handleSubmit} = props;

  const onFormSubmit = useCallback((values) => {
    
    props.handleDrawerForm(values);
    if(Object.values(values).includes(false)){
      setChecked(false)
    }
  }, []);
  const reduxState = useSelector(
        (state) => ({
          diaryType: state.diary.diaryType,
        }),
        shallowEqual
      );

  const [checked, setChecked] = useState(false);
  const [filterOptions, setFilterOptions] = useState([{}]);

  const appointmentOptions = [
    {
      label: "Open Home",
      value: "openHome",
    },
    {
      label: "Buyer",
      value: "buyer",
    },
    {
      label: "General",
      value: "general",
    },
  ];

  const taskOptions = [
    {
      label: "Buyers to Call",
      value: "Buyers to Call",
    },
    {
      label: "Contacts to call",
      value: "Contacts to call",
    },
    {
      label: "Listings Checklist",
      value: "Listings Checklist",
    },
    {
      label: "Prospects to Call",
      value: "Prospects to Call",
    },
    {
      label: "To Do",
      value: "To Do",
    },
  ]

  useEffect(() => {

    if(reduxState.diaryType === 'tasks')
    {
      setFilterOptions(taskOptions)
    }
    else {
      setFilterOptions(appointmentOptions)
    }

  }, [reduxState.diaryType]);


  const handleAllSelect = (e) => {
    setChecked(e.currentTarget.checked);

    if (e) {
      appointmentOptions.forEach((element) => {
        props.change(element.value, e.currentTarget.checked);
        console.log(element);
      });
    }
  };
  console.log(reduxState.diaryType)
  return (
    <form
      onChange={() =>
        setTimeout(handleSubmit((params) => onFormSubmit(params)))
      }
      className=" diary-filter overflow-auto"
    >
      <div className="d-flex pb-1">
        <ButtonGroup toggle className="lightGrey-button">
          <ToggleButton
            type="checkbox"
            checked={checked}
            value="1"
            onChange={handleAllSelect}
          >
            All
          </ToggleButton>
        </ButtonGroup>
      </div>

      {filterOptions?.map((option, i) => (
        <div className="d-flex pb-1">
          <Field
            name={option.value}
            classNames={option.value + "-button"}
            component={SingleToggleButton}
            value={option.value}
            label={option.label}
          />
        </div>
      ))}
    </form>
  );
};

export default reduxForm({
  form: "diaryFilter", // a unique identifier for this form
  initialValues: {},
})(DrawerForm);
